$primary-color: #02091C;
$secondary-color: #FFFF;
$terciary-color: #7F8087;

body {
  margin: 0;
  padding: 0;
  background-color: $primary-color; // Use primary color for the entire page
  color: #ffffff; // Optional: Set default text color for content
  font-family: Arial, sans-serif; // Optional: Set a global font
}

// Header styles
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* Remove explicit background color for header */
  padding: 1rem 2rem;

  .title-image {
    height: auto;
    max-height: 50px;
  }

  .menu-icon {
    height: auto;
    max-height: 30px;
    cursor: pointer;
  }
}